import React, { Component } from "react";
import ScrollHelper from "../ScrollHelper/ScrollHelper";
import Blackbox from "../Blackbox/Blackbox";
import "./Info.scss";
import InfoCardShort from "./InfoCardShort/InfoCardShort";
import InfoCardLong from "./InfoCardLong/InfoCardLong";
import FeelTheReel from "./FeelTheReel/FeelTheReel";
import Offer from "./Offer/Offer";
import FeelTheReelLong from "./FeelTheReelLong/FeelTheReelLong";
import OfferLong from "./OfferLong/OfferLong";

export default class Info extends Component {
  render() {
    const {
      openExtendedInfo,
      closeExtendedInfo,
      openShowInfo,
      closeShowInfo,
      openOfferInfo,
      closeOfferInfo,
      isExtendedInfoOn,
      areImagesDisplayed,
      isShowInfoOn,
      isOfferInfoOn
    } = this.props;

    return (
      <section className="page-info-card">
        <Blackbox
          isOn={isExtendedInfoOn}
          clickAction={closeExtendedInfo}
          height="-100vh"
        >
          <div className="container">
            <InfoCardLong isOn={areImagesDisplayed ? true : false} />
          </div>
        </Blackbox>
        <Blackbox
          isOn={isShowInfoOn}
          clickAction={closeShowInfo}
          height="-100vh"
        >
          <div className="container">
            <FeelTheReelLong />
          </div>
        </Blackbox>
        <Blackbox
          isOn={isOfferInfoOn}
          clickAction={closeOfferInfo}
          height="-100vh"
        >
          <div className="container">
            <OfferLong />
          </div>
        </Blackbox>
        <div className="container info-container">
          <InfoCardShort clickMethod={openExtendedInfo} />
          <FeelTheReel clickMethod={openShowInfo} />
          <Offer clickMethod={openOfferInfo} />
        </div>
        <ScrollHelper className="page-videos-scroll" height="100px" />
      </section>
    );
  }
}
