import React, { Component } from "react";

import Header from "../Components/Header/Header";
import Slider from "../Components/Slider/Slider";
import Gallery from "../Components/Gallery/Gallery";
import Videos from "../Components/Videos/Videos";
import Info from "../Components/Info/Info";
import Footer from "../Components/Footer/Footer";
import {
  faYoutube,
  faFacebook,
  faInstagram
} from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from "@fortawesome/free-regular-svg-icons";
import { faMobileAlt } from "@fortawesome/free-solid-svg-icons";
import "./App.scss";

// library.add(faYoutube);

class App extends Component {
  state = {
    isExtendedInfoOn: false,
    areImagesDisplayed: false,
    isShowInfoOn: false,
    isOfferInfoOn: false,
    isGalleryOpen: false
  };

  openExtendedInfo = () => {
    this.setState({
      isExtendedInfoOn: true,
      areImagesDisplayed: true
    });
  };

  closeExtendedInfo = () => {
    this.setState({ isExtendedInfoOn: false });
  };

  openShowInfo = () => {
    this.setState({
      isShowInfoOn: true
    });
  };

  closeShowInfo = () => {
    this.setState({
      isShowInfoOn: false
    });
  };
  openOfferInfo = () => {
    this.setState({
      isOfferInfoOn: true
    });
  };
  closeOfferInfo = () => {
    this.setState({
      isOfferInfoOn: false
    });
  };

  openGallery = () => {
    this.setState({
      isGalleryOpen: true
    });
  };

  closeGallery = () => {
    this.setState({
      isGalleryOpen: false
    });
  };

  render() {
    const {
      isExtendedInfoOn,
      areImagesDisplayed,
      isShowInfoOn,
      isOfferInfoOn,
      isGalleryOpen
    } = this.state;

    return (
      <div
        className="App"
        style={
          isGalleryOpen
            ? { overflow: "hidden", height: "100vh" }
            : { overflow: "auto" }
        }
      >
        <Header
          openExtendedInfo={this.openExtendedInfo}
          closeExtendedInfo={this.closeExtendedInfo}
          openShowInfo={this.openShowInfo}
          closeShowInfo={this.closeShowInfo}
          openOfferInfo={this.openOfferInfo}
          closeOfferInfo={this.closeOfferInfo}
        />
        <Slider />
        <Info
          openExtendedInfo={this.openExtendedInfo}
          closeExtendedInfo={this.closeExtendedInfo}
          openShowInfo={this.openShowInfo}
          closeShowInfo={this.closeShowInfo}
          openOfferInfo={this.openOfferInfo}
          closeOfferInfo={this.closeOfferInfo}
          isExtendedInfoOn={isExtendedInfoOn}
          areImagesDisplayed={areImagesDisplayed}
          isShowInfoOn={isShowInfoOn}
          isOfferInfoOn={isOfferInfoOn}
        />
        <Videos />
        <Gallery
          openGallery={this.openGallery}
          closeGallery={this.closeGallery}
        />
        <Footer
          icons={{
            youtube: faYoutube,
            facebook: faFacebook,
            instagram: faInstagram,
            mail: faEnvelope,
            phone: faMobileAlt
          }}
        />
      </div>
    );
  }
}

export default App;
